<template>
  <div id="app" :class="{ 'is-component': isComponent }">
    <df-container style="height:100%;overflow:hidden">
      <df-header class="main-header" height="80">
        <Header />
      </df-header>
      <df-container class="main-container">
        <div class="page-container">
          <router-view />
        </div>
      </df-container>
    </df-container>
  </div>
</template>

<script>
import Header from './components/header'

export default {
  components: {
    Header
  },
  computed: {
    isComponent () {
      const path = this.$route.path || ''
      return path.search(/component/i) > -1
    }
  },
  watch: {
    $route: {
      immediate: true,
      handler (value) {
        this.$nextTick(() => {
          this.setCopyCodeButton() // , 
          if (['/component/classes', '/css/classes'].includes(value.path)) this.setColorBlock()
        })
      }
    }
  },
  methods: {
    setColorBlock () {
      const cssWrapper = document.getElementsByClassName('language-css')[0]
      const cssValues = Array.from(cssWrapper.querySelectorAll('.hljs-number'))
      this.mapAddColorBlock(cssValues, 'css')
      const jsWrapper = document.getElementsByClassName('language-js')[0]
      const jsValues = Array.from(jsWrapper.querySelectorAll('.hljs-string'))
      this.mapAddColorBlock(jsValues, 'js')
    },
    mapAddColorBlock (colorValues, wrapperType) {
      colorValues.forEach(colorValue => {
        const color = wrapperType === 'css' ? colorValue.innerText : colorValue.innerText.substring(1, colorValue.innerText.length - 1)
        if (!color.includes('#')) return
        const colorBlock = document.createElement('div')
        colorBlock.classList.add('css-color-block')
        colorBlock.style.backgroundColor = color
        colorValue.appendChild(colorBlock)
      })
    },
    setCopyCodeButton () {
      const allPres = Array.from(document.getElementsByTagName('pre')) // ,  NodeList,  Array,  Array 
      allPres.forEach(pre => { // 
        if (!pre.querySelector('.copy-code')) { // , 
          const copyButton = document.createElement('div') // 
          copyButton.innerText = '' // 
          copyButton.classList.add('copy-code') // 
          copyButton.onclick = (e) => { // 
            this.copyElementContent(e.target.nextSibling) // 
          }
          const codeNode = pre.querySelector('code') // 
          pre.insertBefore(copyButton, codeNode) // 
        }
      })
    },
    copyElementContent (dom) { //  dom 
      dom.setAttribute('contenteditable', true) //  div 
      dom.focus() // 
      window.getSelection().selectAllChildren(dom) //  getSelection ,  dom.select() ,  select 
      document.execCommand('copy') // 
      window.getSelection().removeAllRanges() //  getSelection 
      dom.setAttribute('contenteditable', false) //  div 
      this.$message({type: 'success', message: ''})
    }
  }
}
</script>

<style>
.css-color-block {
  width: 12px;
  height: 12px;
  display: inline-block;
  margin-bottom: -1px;
  margin-left: 5px;
}
.copy-code {
  position: absolute;
  right: 20px;
  top: 20px;
  padding: 4px 6px;
  z-index: auto;
  background: #e3e3e3;
  border-radius: 4px;
  color: #464646;
  cursor: pointer;
}
.copy-code:hover {
  background: #d6d6d6;
}
.copy-code:active {
  background: #c6c6c6;
}
</style>
